.heart {
  font-size: 2rem;
  color: red;
  animation: float 2s ease-in-out infinite;
  opacity: 0;
  transform: translateY(100%);
}

@keyframes float {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  50% {
    opacity: 1;
    transform: translateY(-20px);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}

.images_flot {
  animation: images_flot 2s ease-in-out infinite;
  transform: translateY(100%);
  opacity: 0;
  height: 100px;
  width: 100px;
  border-radius: 50%;
}

@keyframes images_flot {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  50% {
    opacity: 1;
    transform: translateY(-20px);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}

/* Heart card styling */
.heart-card {
  position: relative;
  width: 150px;
  height: 150px;
  background-color: red;
  transform: rotate(-45deg);
  margin: 0 auto;
}

.heart-card::before,
.heart-card::after {
  content: '';
  position: absolute;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: red;
}

.heart-card::before {
  top: -75px;
  left: 0;
}

.heart-card::after {
  left: 75px;
  top: 0;
}

/* Center image inside the heart */
.heart-image {
  transform: rotate(45deg); /* Rotate back to make image straight */
}

/* Overlay background for the modal effect */
.overlay {
  background: rgba(0, 0, 0, 0.5);
}

/* Animation for the heart card */
.heart-card {
  animation: fade-in 0.5s ease forwards;
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: scale(0.8) rotate(-45deg);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(-45deg);
  }
}
